@import "https://fonts.googleapis.com/css2?family=Unbounded:wght@400;700&display=swap";
:root {
  --color-light: #f1faee;
  --color-dark: #1d3557;
  --color3: #e63946;
  --color4: #457b9d;
  --color5: #a8dadc;
