  overflow-wrap: break-word;
  color: var(--color4);
  opacity: 1;
  font-size: 2.5rem;
  list-style: none;
  transition: opacity 1s;
  animation: 25s linear infinite animateTranslate, 25s infinite animateRotate;
  display: block;
  position: absolute;
  bottom: -10rem;
  overflow: hidden;
}

.digits li.hide {
