  border-radius: 10px;
  justify-content: flex-start;
  align-items: center;
  padding: 1rem .5rem;
  font-size: 18px;
  transition: all .25s;
  display: flex;
  position: relative;
  overflow: hidden;
}

.memorize-field {
  white-space: normal;
  height: 10rem;
}

.result-container span::selection {
  background-color: var(--color3);
  color: var(--color-light);
}

.result-container #result {
  max-width: calc(100% - 120px);
