.checkboxes-container {
  margin: 0 1.5rem 2rem;
}

.checkboxes-container.random .setting.memorize, .checkboxes-container.memorize .setting.random, .checkboxes-container.pin .setting.random, .checkboxes-container.pin .setting.memorize {
  display: none;
}

.setting-description {
  font-size: 1.5rem;
}

.toggler-wrapper {
  z-index: 1;
  align-items: center;
  display: flex;
}

.toggler {
  height: 1.75rem;
  width: 3rem;
  display: inline-block;
  position: relative;
}

.toggler input {
  display: none;
}

.slider {
  background: var(--color-dark);
  cursor: pointer;
  transition: all .4s;
  position: absolute;
  inset: 0;
}

.slider:before {
  background: var(--color3);
  content: "";
  height: 1.25rem;
  width: 1.25rem;
  transition: all .25s;
  position: absolute;
  bottom: .25rem;
  left: .25rem;
  box-shadow: 3px 3px 6px #00000080;
}

input:checked + .slider {
  background: var(--color5);
}

input:checked + .slider:before {
  background: var(--color-light);
  transform: translateX(1.25rem)scale(1.2);
}

.slider.round, .slider.round:before {
  border-radius: 2rem;
}

.generate-button {
  width: 100%;
  height: 3rem;
  background-color: var(--color-dark);
  color: var(--color-light);
  cursor: pointer;
  z-index: 1;
  transition: transform .1s ease box-shadow .1s ease;
  border: none;
  border-radius: 10px;
  font-family: inherit;
  font-size: 1.5rem;
  display: block;
  position: relative;
  overflow: hidden;
  box-shadow: 0 2px 4px #00000080;
}

.generate-button:before {
  content: "";
  filter: blur(1rem);
  width: 100%;
  background-color: var(--color4);
  z-index: -1;
  transition: left .25s ease-in;
  position: absolute;
  top: 0;
  bottom: 0;
  left: -200%;
  transform: scale(2);
}

.generate-button:hover:before {
